import React ,{useEffect ,useState} from 'react';
import { connect } from 'react-redux';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  CardSubtitle,
  Row,
} from 'reactstrap';
import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import { Colxx } from '../../../../components/common/CustomBootstrap';
import RecentHack from '../../../../components/common/RecentHack';
import EditProfileActions from './EditProfileActions';
import UserCard from './UserCard';
import useFetch from '../../../../hooks/use-fetch';
import { getCurrentUser, log } from '../../../../helpers/Utils';
import { adminRoot } from '../../../../constants/defaultValues';
import IntlMessages from '../../../../helpers/IntlMessages';

const ProfileView = ({ match, currentUser, profileData }) => {
  const { id } = getCurrentUser();
  // const { uid } = getProfileData();
  const [user, setUser] = useState();
  const { loading, data, error } = useFetch(`${id}`);

  useEffect(() => {
    fetch(`http://api.smart-natives.de/index.php/api/userhackathonlist/${id}`)
    .then(res => {
      return res.json();
    })
    .then(data1 => {
    setUser(data1);
    });
  }, [id]);

  if (loading) return <div className="loading" />;

  if (error) log(error);
  console.log(user);
  console.log(id);
  console.log(currentUser);
  console.log(profileData);

  const userLength = user ? user.length : 0;

  
  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Row>
            <Colxx className="mb-0">
              <h1>{profileData?.full_name}</h1>
              <EditProfileActions />
            </Colxx>
          </Row>

          <Breadcrumb match={match} />

          <Row>
            <Colxx xxs="12" lg="5" className="mb-4 col-left">
              <UserCard currentUser={currentUser} />
            </Colxx>

            <Colxx xxs="12" lg="7" className="mb-4 col-right">
              <Row>
                {userLength ? (
                  user.map((hack) => {
                    return (
                      <Colxx xxs="12" lg="6" className="mb-4" key={hack.id}>
                        <RecentHack data={hack} />
                      </Colxx>
                    );
                  })
                ) : (
                  <Colxx xxs="12">
                    <Card className="text-center">
                      <CardBody>
                        <CardTitle className="mb-0">
                          You have applied to{data.length}
                        </CardTitle>

                        <div className="display-1">{user.length}</div>
                        <CardSubtitle>Hackathons</CardSubtitle>

                        <div>
                          <Button
                            href={`${adminRoot}/challenges/explore`}
                            color="primary"
                          >
                            <IntlMessages id="challenges.explore-all" />
                          </Button>
                        </div>
                      </CardBody>
                    </Card>
                  </Colxx>
                )}
              </Row>
            </Colxx>
          </Row>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { currentUser, profileData } = authUser;
  return { currentUser, profileData };
};

const mapActionsToProps = {};

export default connect(mapStateToProps, mapActionsToProps)(ProfileView);