import React, { useState } from 'react';
import {
  // Badge,
  Card,
  Button,
  CardBody,
  NavLink as Link,
  Modal,
  ModalBody,
  InputGroupAddon,
  CustomInput,
  InputGroup,
  ModalHeader,
} from 'reactstrap';
import { NavLink } from 'react-router-dom';
import SingleLightbox from '../../../../components/pages/SingleLightbox';
import { adminRoot } from '../../../../constants/defaultValues';

import IntlMessages from '../../../../helpers/IntlMessages';
import { getProfileData, getProfileDataAsync } from '../../../../helpers/Utils';
import instance from '../../../../helpers/axiosInstance';

const UserCard = () => {
  const profileData = getProfileData();
  const [values, setValues] = useState({});
  const [openUpload, setOpenUpload] = useState(false);

  const formData = new FormData();

  const handleFileChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.files[0] });
  };

  const handleUpload = async (event) => {
    console.log(profileData);
    formData.append('id', profileData.id);
    formData.append('user_id', profileData.user_id);
    formData.delete(event.target.id);

    formData.append(`${event.target.id}`, values[event.target.id]);

    await instance({
      url: '/updateProfile',
      method: 'POST',
      headers: {
        'Content-Type': 'multipart/form-data',
      },
      data: formData,
      timeout: 100000,
    });

    await getProfileDataAsync(profileData?.user_id);
    setOpenUpload(false);
    window.location.reload();
  };

  const skillStr = profileData.other_skills;
  const userSkills = skillStr.split(";");
  const skillLength = userSkills ? userSkills.length : 0;

  return (
    <Card className="mb-4">
      <div className="position-absolute card-top-buttons">
        <Button
          outline
          color="primary"
          className="icon-button"
          onClick={() => setOpenUpload(true)}
        >
          <i className="simple-icon-pencil" />
        </Button>
      </div>

      {profileData?.profile_photo && (
        <SingleLightbox
          thumb={profileData?.profile_photo}
          large={profileData?.profile_photo}
          className="card-img-top"
        />
      )}

      <CardBody>
        <p className="text-muted text-small">
          <IntlMessages id="menu.about" />
        </p>

        <p className="mb-3">
          {profileData?.bio || (
            <NavLink to={`${adminRoot}/profile/about`}>
              {profileData?.bio}
            </NavLink>
          )}
        </p>
        {/* 
        <p className="text-muted text-small mb-2">
          <IntlMessages id="pages.location" />
        </p> */}

        <p className="mb-3">
          {profileData?.address || (
            <NavLink to={`${adminRoot}/profile/contact`}>
              {profileData?.address}
            </NavLink>
          )}
        </p>

        {/* <p className="text-muted text-small mb-2">
          <IntlMessages id="pages.responsibilities" />
        </p>

        <p className="mb-3">
          <Badge color="outline-secondary" className="mb-1 mr-1" pill>
            FRONTEND
          </Badge>
          <Badge color="outline-secondary" className="mb-1 mr-1" pill>
            JAVASCRIPT
          </Badge>
          <Badge color="outline-secondary" className="mb-1 mr-1" pill>
            SECURITY
          </Badge>
          <Badge color="outline-secondary" className="mb-1 mr-1" pill>
            DESIGN
          </Badge>
        </p> */}

        <p className="text-muted text-small mb-2">
          <IntlMessages id="menu.contact" />
        </p>

        <div className="social-icons">
          <ul className="list-unstyled list-inline">
            <li className="list-inline-item">
              <Link href={profileData?.facebook} target="_blank">
                <i className="simple-icon-social-facebook" />
              </Link>
            </li>
            <li className="list-inline-item">
              <Link href={profileData?.linkedin}>
                <i className="simple-icon-social-linkedin" />
              </Link>
            </li>
            <li className="list-inline-item">
              <Link href={profileData?.instagram} target="_blank">
                <i className="simple-icon-social-instagram" />
              </Link>
            </li>
            <li className="list-inline-item">
              <Link href={profileData?.github} target="_blank">
                <i className="simple-icon-social-github" />
              </Link>
            </li>
          </ul>
        </div>

        <p className="text-muted text-small mb-2">
          <IntlMessages id="menu.skills" />
        </p>
        <div>
          <ul className="list-unstyled list-inline">
            {skillLength ? (
              userSkills.map((skill)=>{
                return <li className="list-inline-item" key="1"><span className="badge badge-pill badge-dark py-2 px-4">{skill}</span></li>
              })
            ) : (
              <li className="list-inline-item"><p>Skills not added</p></li>
            )}
          </ul>
        </div>
      </CardBody>
    
      <Modal isOpen={openUpload} toggle={() => setOpenUpload(!openUpload)}>
        <ModalHeader>
          <IntlMessages id="user.upload-profile-photo" />
        </ModalHeader>

        <ModalBody>
          <InputGroup>
            <CustomInput
              type="file"
              id="profile_photo"
              name="customFile"
              accept="image/jpeg, image/jpg, image/png"
              onChange={handleFileChange}
            />
            <InputGroupAddon addonType="append">
              <Button
                outline
                color="secondary"
                id="profile_photo"
                onClick={handleUpload}
              >
                <IntlMessages id="user.upload-button" />
              </Button>
            </InputGroupAddon>
          </InputGroup>
        </ModalBody>
      </Modal>
    </Card>
  );
};

export default UserCard;