/* eslint-disable camelcase */
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardSubtitle,
  CardText,
  CardTitle,
  // NavLink,
  Row,
} from 'reactstrap';
import { adminRoot } from '../../constants/defaultValues';
import useRequest from '../../hooks/use-request';
import { log } from '../../helpers/Utils';
import IntlMessages from '../../helpers/IntlMessages';
import { Separator } from './CustomBootstrap';

const getStatusColor = (status) => {
  if (status === 0) return 'outline-success';
  if (status === 1) return 'outline-warning';
  if (status === 'COMPLETED') return 'outline-danger';
  return '';
};

const getStatusText = (status) => {
  if (status === 0) return 'UPCOMING';
  if (status === 1) return 'ON GOING';
  if (status === 2) return 'COMPLETED';
  return 'primary';
};

const getTypeText = (status) => {
  if (status === 0) return 'Online';
  if (status === 1) return 'Blender';
  if (status === 2) return 'Offiline';
  return '';
};

const RecentHack = ({ data, participated }) => {
  const {
    id,
    name,
    tagline,
    submit_status,
    hackathon_start,
    application_end,
    hackathon_website,
    hackathon_type_id,
    otherArray,
  } = data;

  const history = useHistory();

  const [data1, setData1] = useState({});
  const [isLoaded, setIsLoaded] = useState(false);

  const [doRequest, requestLoading, requestError] = useRequest({
    url: `/getdetails/${data.id}`,
    method: 'GET',

    nextSuccess: (responseData) => {
      setData1(responseData.getdetails);
      setIsLoaded(true);
    },
  });

  useEffect(() => {
    doRequest();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (requestLoading || !isLoaded) return <div className="loading" />;

  if (requestError) log(requestError);

  console.log(data1);

  let sdg = otherArray?.sdg_goals;
  let mega = otherArray?.mega_trends;

  if (sdg === false) {
    sdg = null;
  }
  if (mega === false) {
    mega = null;
  }

  return (
    <Card style={data1.hackathon_winner.length > 0 ? { background: '#174F8F10' } : {}}>
      {data1.hackathon_winner.length > 0 && (
        <Button
          className="mx-3"
          style={{ fontSize: '1rem' }}
          onClick={() => history.push(`${adminRoot}/challenges/${id}/results`)}
        >
          <span role="img" aria-label="popper">
            ðŸŽ‰
          </span>{' '}
          Results announced{' '}
          <span role="img" aria-label="popper">
            ðŸŽ‰
          </span>
        </Button>
      )}
      <CardBody>
        <CardTitle>
          <p className="h2 lead">
            <a
              href={hackathon_website}
              target="_blank"
              rel="noreferrer"
              className="new-tab"
            >
              {name}
            </a>
          </p>
          <CardSubtitle className="text-small">{tagline}</CardSubtitle>
        </CardTitle>

        <Row className="justify-content-center">
          {sdg?.map((item) => (
            <img
              key={item.id}
              src={`/assets/img/sdg/${item.id}.png`}
              alt={item.name}
              className="list-thumbnail align-self-center m-1 small hackcard-icon"
            />
          ))}
          |{/* <div className="vertical" style={{ height: '45px' }} /> */}
          {mega?.map((item) => (
            <img
              key={item.id}
              src={`/assets/img/mega-trends/${item.id}.png`}
              alt={item.name}
              className="list-thumbnail align-self-center m-1 small hackcard-icon"
            />
          ))}
        </Row>

        <Separator className="my-4" />

        <CardTitle>
          <IntlMessages id="Application Start" />
          <CardText>{application_end}</CardText>
        </CardTitle>

        <CardTitle>
          <IntlMessages id="hackathon start date" />
          <CardText>{hackathon_start}</CardText>
        </CardTitle>

        <Separator className="mb-4" />

        <Badge
          color={getStatusColor(submit_status)}
          pill
          outline
          className="mb-3 mr-3"
        >
          {getStatusText(submit_status)}
        </Badge>

        <Badge color="outline-primary" pill className="mb-3">
          {getTypeText(hackathon_type_id)}
        </Badge>

        <Row className="justify-content-center">
          {!data1.hackathon_winner.length > 0 ? (
            <>
              {participated ? (
              <Button
                color="secondary"
                className="m-1"
                onClick={() =>
                  history.push(`${adminRoot}/challenges/${id}/submission`)
                }
              >
                <IntlMessages id="Submit your hack" />
              </Button>
              ) : (
              <Button
                color="secondary"
                className="m-1"
                onClick={() =>
                  history.push(`${adminRoot}/challenges/register/${id}`)
                }
              >
                <IntlMessages id="hackcard.participate-button" />
              </Button>
              )}
            </>
          ) : (
            <></>
          )}
          <Button
            color="secondary"
            className="m-1"
            onClick={() => history.push(`${adminRoot}/challenges/${id}`)}
          >
            <IntlMessages id="hackcard.view-details" />
          </Button>
        </Row>

        {/* <pre>{JSON.stringify(data, null, 2)}</pre> */}
      </CardBody>
    </Card>
  );
};

export default RecentHack;
